.header{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 70px;
    height: 150%;
    transition: 1s;
    flex-direction: column;
    /* margin-right: 5px; */


    background-color: #1F2833 ;
    color: #C5C6C7;
}
.header a{
    text-decoration: none;
    color: #C5C6C7;
    margin-top: 10px;
}



@media screen and (max-width: 600px) {
    .header{
        transform: translate(-73px,0);
        width: 0;
        position: fixed;
    z-index: 100;
    }
    .open{
    transform: translate(0,0);
    width: 70px;
   
    }
}