@import './DoTo.scss';

.todos_cont {
    overflow-y: auto;
    height: calc(100% - 25px);
    width: 100%;
    display: flex;
    // grid-template-columns: 1fr 1fr;
    //  flex-direction: column; 
    // flex-wrap: wrap;
    // align-items: center;
    // justify-items: stretch;
    // padding-bottom: 100px;

    // &:hover {
    //     background-color: green;
    // }

}

@mixin container($align){
    width: 50%;
    height: 100%;
    display: flex;
    align-items: $align;
    // align-items:flex-end;
    flex-direction: column;
    // justify-content:$justify ;
    // align-items: center;
}

.not_done_container{
    @include container(flex-start)
}
.done_container{
    @include container(flex-end)
}
// ;

.todo_all {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: relative;
}

p{
    margin: 0;
}

@mixin BOX($width, $margin, $self_justify) {
    background-color: aquamarine;
    // width: $width;
    height: 55px;
    width: 40%;
    min-width: 100px;
    display: flex;
    justify-content: space-between;
    justify-self: $self_justify;
    align-items: center;
    margin: $margin ;
    // position: absolute;
    // top: 0;

    // left: 0;
    &:hover {
        background-color: yellow;
    }

    // margin-bottom: change();
    // @include ss
}

// .box {
//  @include BOX(200px,10px)
// }
.not_done {
    @include BOX(200px, 10px, start)
}

.done {
    @include BOX(200px, 10px, end)
}







































































