.countries_cont{
    display: flex;
    /* flex-direction: column; */
    flex-wrap: wrap;
    height: calc(100vh - 38px);
    width: 100%;
    /* align-items: center; */
    
    justify-content:center;
     overflow-y: auto;   
    
}
.country_cont{
    margin: 10px;
    width: 200px;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: right;
    text-align: left;
    background-color: rgba(0, 0, 0, 0.4);
    border: black 1px solid;
    position: relative;
}
.countries_cont p{
    left: 0;
    position: absolute;
    width: 100px;
}
.flag{
    width: 100px;
    height: 100px;
    margin-left: 3px;
    
}
.input{
    margin-top: 5px;
    height: 20px;
    margin-bottom: 5px;
    padding: 2px;
}
.all_cont{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}


